// Dependancy Import
import React from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

// Component Import
import './Navbar.css';
import icon from '../Images/marshmallow_trans.png';

const linkAnim = {
    in: {
        borderBottom: "0px",
    },
    hover: {
        borderBottom: "1px solid white",
    }
}


function Navbar(props) {
    var path = props.location.pathname
    return (
        <nav>
            <Link to="/"><img src={icon} alt="A marshmello" width="100px" id="home" /></Link>
            <ul>
                <li>
                {path === "/about" ? 
                <span id="activePage">about</span>: 
                <Link to="/about"><motion.span 
                variants={linkAnim} initial="in" 
                whileHover="hover">about
                </motion.span></Link>}
                </li>
                <li>
                {path === "/work" ? 
                <span id="activePage">work</span>: 
                <Link to="/work"><motion.span 
                variants={linkAnim} initial="in" 
                whileHover="hover">work
                </motion.span></Link>}
                </li>
            </ul>
        </nav>
    )
}


export default Navbar;