import React from 'react';
import { motion } from 'framer-motion';
import './ScrollToTop.css';

const buttonAnim = {
    in: {
        opacity: 0,
        y: "10vh",
    },
    out: {
        opacity: 1,
        y: 0,
        transition: {
            type: 'spring',
            duration: 1,
        }
    },
    hover: {
        x: "-0.2em",
        y: "-0.2em",
        boxShadow: "0.2em 0.2em #d7d8f4",
        transition: {
            duration: 0.2,
        }
    },
    exit: {
        opacity: 0,
        y: "10vh",
        transition: {
            duration: 0.3,
        }
    }
};

function scrollToTop() {
    window.scrollTo({
        top: 0,
        behavior: "smooth",
    })
}

function ScrollToTop() {
    return (
        <motion.div
            onClick={scrollToTop}
            id="scrollToTop"
            variants={buttonAnim}
            initial="in"
            animate="out"
            exit="exit"
            whileHover="hover">
            <svg className="w-6 h-6" fill="none"
                stroke="currentColor" viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg">
                <path strokeLinecap="round" strokeLinejoin="round"
                    strokeWidth="1" d="M5 15l7-7 7 7" />
            </svg>
        </motion.div>
    )
}

export default ScrollToTop;