import React from 'react';
import './Navbar.css';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

const sideNavAnim = {
    in: {
        opacity: 0.5,
    },
    hover: {
        opacity: 1,
    }
}


function SideNav(props) {
    var prev = (props.location.pathname === '/about' ? '/work' : '/')
    var next = (props.location.pathname === '/' ? '/work' : '/about')
    return (
        <>
            {props.location.pathname !== '/' ?
                <Link to={prev}><motion.svg variants={sideNavAnim} initial="in" whileHover="hover"
                    id="left" className="w-6 h-6 sideNav" fill="none"
                    stroke="currentColor" viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg">
                    <path strokeLinecap="round" strokeLinejoin="round"
                        strokeWidth="2" d="M15 19l-7-7 7-7" /></motion.svg></Link>
                : <> </>}

            {props.location.pathname !== '/about' ?
                <Link to={next}><motion.svg variants={sideNavAnim} initial="in" whileHover="hover"
                    id="right" className="w-6 h-6 sideNav" fill="none"
                    stroke="currentColor" viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg">
                    <path strokeLinecap="round" strokeLinejoin="round"
                        strokeWidth="2" d="M9 5l7 7-7 7" /></motion.svg></Link>
                : <> </>}
        </>
    )
}

export default SideNav;