// Dependancy Import
import React from 'react';

import './Card.css';

function Card(props) {
    if (props.name) {
        return (
            <div className="card">
                <img src={props.img} alt={props.alt} /><br />
                <p id="type">{props.type}</p>
                <h2>{props.name}</h2>
                <p>{props.desc}</p>
            </div>
        )
    }
    return(
        <></>
    )
}

export default Card;