// Dependancy Import
import React from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

// Component Import
import './Work.css';
import Card from '../../Components/Card/Card';
import WorkList from './WorkList/WorkList';
import ScrollToTop from '../../Components/ScrollToTop/ScrollToTop';

const containerAnim = {
    in: {
        opacity: 0,
        x: "-10vw",
    },
    out: {
        opacity: 1,
        x: 0,
        transition: {
            type: 'spring',
            duration: 1,
        }
    },
    exit: {
        opacity: 0,
        x: "-10vw",
        transition: {
            ease: 'easeInOut',
        }
    }
};

const ulAnim = {
    out: {
        transition: {
            delayChildren: 0.7,
            staggerChildren: 0.2,
            ease: 'easeInOut',
        }
    }
}

const CardAnim = {
    in: {
        opacity: 0,
    },
    out: {
        opacity: 1,
        transition: {
            type: 'spring',
            duration: 1,
        }
    },
}

const WorkCards = ({ projects }) => {
    const cardsArr = projects.map(work => (
        <motion.li key={work.name} variants={CardAnim}>
            <Card name={work.name} img={work.img} desc={work.desc} type={work.type} />
        </motion.li>
    ))
    return (
        <>
            {cardsArr}
            <motion.p variants={CardAnim} id="more">
                and more...<br />(hopefully)<br /><br />
            </motion.p>
        </>
    )
}

function Work() {
    window.scrollTo({
        top: 0,
    })
    return (
        <>
            <motion.div className="work"
                variants={containerAnim}
                initial="in"
                animate="out"
                exit="exit"
            >
                <h1>Work</h1>
                <motion.ul variants={ulAnim}>
                    <WorkCards projects={WorkList} />
                </motion.ul>
            </motion.div>
            <ScrollToTop />
        </>
    )
}



export default Work;
