// Dependancy Import
import React from 'react';
import { motion } from 'framer-motion';

// Component Import
import './Home.css';
import icon from '../../Components/Images/marshmallow_shadow.png';

const containerAnim = {
  in: {
    opacity: 0,
    x: 0,
  },
  out: {
    opacity: 1,
    x: 0,
    transition: {
      type: 'spring',
      duration: 0,
    }
  },
  exit: {
    opacity: 0,
    x: "-10vw",
    transition: {
      ease: 'easeInOut',
    }
  }
};

const iconAnim = {
  in: {
    x: "-10%",
    opacity: 0,
  },
  out: {
    x: 0,
    opacity: 1,
    transition: {
      duration: 1,
    }
  }
};

const titleAnim = {
  in: {
    x: "10%",
    opacity: 0,
  },
  out: {
    x: 0,
    opacity: 1,
    transition: {
      delay: 0.8,
      duration: 0.7,
    },
  },
}

const hoverAnim = {
  backgroundColor: "#d7d8f4",
  borderRadius: "50px",
  transition: {
    duration: 0.5
  }
}

function Home() {

  return (
    <motion.div className="home"
      variants={containerAnim}
      initial="in"
      animate="out"
      exit="exit"
    >
      <motion.img variants={iconAnim} src={icon} alt="A Big Marshmallow" width="30%"/>
      <motion.h1 variants={titleAnim} id="marshchoco">
        <span></span>Hi, I am<br />
        <motion.a id="name" href="https://marshmellochoco.github.io" whileHover={hoverAnim}><b>marshmellochoco</b></motion.a>
      </motion.h1>
      <div className="balls" onClick={e => handleClick()}>
        <button className="ball" id="ball1"></button>
        <button className="ball" id="ball2"></button>
        <button className="ball" id="ball3"></button>
      </div>
    </motion.div>
  )

}

function handleClick() {
  alert("Nope, this button is not going to change the color or something.");
}

export default Home;